<template>
  <div class="business-portal-page create-page-content">
    <div v-if="!createCompleted">
      <portal-header :projectObj="titleConfig" :showBottomLine="false" />
      <div>
        <steps-tabs :activeName="activeName" :tabs-panel="tabsPanel">
          <template slot="first">
            <identity-info ref="identityTab" :identityInfo.sync="identityInfo" :isViewMode="false"></identity-info>
          </template>
          <template slot="second">
            <div class="orgOper-kyc-form">
              <div class="line"></div>
              <p class="title title-padding">{{ $t('IDC.orgOperator.orgOperKycFormT') }}</p>
              <div class="boottm-line"></div>
              <div class="orgOper-kyc-form-padding">
                <keep-alive>
                  <kyc-form
                    ref="OrgOperKycInfo"
                    identityClassify="orgOper"
                    :adaptiveLayout="true"
                    :kycInfo.sync="kycFormData"
                    mode="create"
                    :roleIds="roleIds"
                    v-if="activeName == 'second'"
                  />
                </keep-alive>
              </div>
            </div>
          </template>
          <template slot="third">
            <identity-info :identityInfo="identityInfo" :isViewMode="true"></identity-info>
            <div class="orgOper-kyc-form">
              <div class="line title-padding"></div>
              <p class="title title-padding">{{ $t('IDC.orgOperator.orgOperKycForm') }}</p>
              <div class="boottm-line"></div>
              <div class="orgOper-kyc-form-padding">
                <kyc-form
                  ref="OrgOperKycInfoReview"
                  identityClassify="orgOper"
                  mode="review"
                  :reviewKycData="kycFormData"
                  v-if="activeName == 'third'"
                />
              </div>
            </div>
          </template>
        </steps-tabs>
      </div>
      <footer-tap
        @reset="cancel"
        @previous="primaryStep"
        @next="nextStep"
        @submit="createOrgOperator"
        :submit-loading="submitLoading"
        :active-name="activeName"
        :steps="tabsPanel"
      />
    </div>
    <div v-else>
      <portal-create-success
        :successText="$t('IDC.orgOperator.operationSucceeded')"
        detailText="View Detail >>"
        addText="Continue to Add >>"
        @view-detail="goViewDetail"
        @continue-add="goContinueToAdd"
      ></portal-create-success>
    </div>
  </div>
</template>

<script>
import IdentityInfo from './components/identity-info.vue';
import StepsTabs from '@/components/stepsTabs';
import PortalHeader from '@/components/portal-header';
import PortalCreateSuccess from '@/components/portal-create-success';
import { addOrgOperator, checkInputKyc } from '@/views/identifier-center/api/api-organization-operator';
import footerTap from '@/components/portal-footer-tap';
import KycForm from '@/components/kyc-common/kyc-form/index.vue';

export default {
  name: 'OrganizationOperatorCreate',
  components: { StepsTabs, PortalHeader, IdentityInfo, PortalCreateSuccess, footerTap, KycForm },
  data() {
    return {
      titleConfig: {
        title: this.$t('IDC.orgOperator.createOrgOperator'),
        desc: this.$t('IDC.orgOperator.createOrgOperatorTitle'),
      },
      kycInfo: {}, // KYC 信息
      activeName: 'first',
      submitLoading: false,
      kycFormData: {},
      identityKYCData: [],
      identityInfo: {
        merchantId: '',
        shortCode: '',
        language: '',
        accessChannel: [],
        ruleProfile: '',
        ruleProfileName: '',
        operatorId: '',
        operatorName: '',
        operatorMsisdn: '',
        email: '',
        kycList: [],
        isRoles: '',
        roles: [],
      },
      orgOperatorId: '',
      orgKycForm: {},
      selectedRoleList: [],
      createCompleted: false,
      tabsPanel: [
        {
          name: 'first',
          title: this.$t('IDC.orgOperator.identityInfo'),
          subTitle: this.$t('IDC.orgOperator.enterInfo'),
        },
        {
          name: 'second',
          title: this.$t('IDC.orgOperator.kycInfo'),
          subTitle: this.$t('IDC.orgOperator.enterDetails'),
        },
        {
          name: 'third',
          title: this.$t('IDC.orgOperator.Review'),
          subTitle: this.$t('IDC.orgOperator.pleaseReview'),
        },
      ],
    };
  },
  created() {},
  computed: {
    roleIds() {
      return this.identityInfo.roles.map((item) => item.roleId);
    },
  },
  watch: {
    kycFormData: {
      handler(value) {
        if (value) {
          this.$emit('update:kycInfo', JSON.parse(JSON.stringify(value)));
        }
      },
      deep: true,
    },
  },
  methods: {
    cancel(closeDialog) {
      if (!closeDialog) {
        this.$confirm(this.$t('IDC.orgOperator.cancel'), 'Reset', {
          confirmButtonText: this.$t('common.button.confirm'),
          cancelButtonText: this.$t('common.button.cancel'),
          closeOnClickModal: false,
          type: 'warning',
        }).then(() => {
          this.refreshCurrentPage();
        });
      }
    },
    goViewDetail() {
      let params = {
        orgOperatorId: this.orgOperatorId,
      };
      this.$router.push({
        path: 'detail',
        query: { data: encodeURIComponent(JSON.stringify(params)) },
      });
    },
    goContinueToAdd() {
      this.refreshCurrentPage();
    },
    getCheckInputKycValue() {
      this.identityKYCData = this.$refs.OrgOperKycInfo.getKYCRequestParams();
      this.submitLoading = true;
      let params = {
        roleIds: this.roleIds,
        identityKYCData: this.$refs.OrgOperKycInfo.getKYCRequestParams(),
      };

      checkInputKyc(params)
        .then((res) => {
          this.submitLoading = false;
          if (res.header && res.header.success && res.result) {
            this.activeName = 'third';
          }
        })
        .finally(() => {
          this.submitLoading = false;
        });
    },
    nextStep() {
      if (this.activeName === 'first') {
        this.$refs.identityTab.validate().then((resultTag) => {
          if (resultTag) {
            this.activeName = 'second';
          }
        });
      } else if (this.activeName === 'second') {
        this.$refs.OrgOperKycInfo.validate().then((resultTag) => {
          if (resultTag) {
            this.getCheckInputKycValue();
          }
        });
      }
    },

    primaryStep() {
      if (this.activeName === 'second') {
        this.activeName = 'first';
      } else {
        this.activeName = 'second';
      }
    },
    createOrgOperator() {
      this.selectedRoleList = this.identityInfo.roles.map((ele) => {
        return {
          roleCode: ele.roleCode,
          effectiveTime: ele.effectiveTime,
          expiredTime: ele.expiredTime,
        };
      });
      let param = {
        operatorMsisdn: this.identityInfo.operatorMsisdn,
        merchantId: this.identityInfo.merchantId,
        shortCode: this.identityInfo.shortCode,
        language: this.identityInfo.language,
        operatorName: this.identityInfo.operatorName,
        operatorId: this.identityInfo.operatorId,
        identityRuleProfileId: this.identityInfo.ruleProfile,
        accessChannel: this.identityInfo.accessChannel,
        notificationChannel: this.orgKycForm.notificationChannel,
        email: this.orgKycForm.email,
        phoneNumber: this.orgKycForm.phoneNumber,
        roles: this.selectedRoleList,
        identityKYCData: this.identityKYCData,
      };
      this.submitLoading = true;
      addOrgOperator(param)
        .then((res) => {
          this.submitLoading = false;
          if (res.header && res.header.success) {
            this.createCompleted = true;
            this.orgOperatorId = res.identityId;
          } else {
            this.$message.error(res.header.responseDesc);
          }
        })
        .finally(() => {
          this.submitLoading = false;
        });
    },
  },
};
</script>

