<template>
  <div class="operator-identity-info">
    <div class="line"></div>
    <el-form
      :model="identityInfo"
      ref="identityInfo"
      :rules="isViewMode ? identityRules1 : identityRules"
      label-position="top"
      size="small"
      class="add-form"
    >
      <p class="title title-padding">{{ $t('IDC.orgOperator.identityInfoNo') }}</p>
      <el-divider></el-divider>
      <el-row :gutter="20" class="title-padding">
        <el-col v-bind="colOptions">
          <el-form-item
            prop="shortCode"
            :label="$t('IDC.orgOperator.orgShortCode')"
            :class="isViewMode ? '' : 'icon-starkey'"
          >
            <div v-if="isViewMode">
              <span v-text="identityInfo.shortCode" />
            </div>
            <el-input
              id="shortCode"
              v-else
              v-model="identityInfo.shortCode"
              ref="saveTagInput"
              clearable
              :placeholder="$t('common.placeholder.select')"
              @clear="clearFn"
            >
              <i class="el-icon-more" slot="suffix" @click="dialogVisible = true"></i>
            </el-input>
            <div class="shortCode" v-if="isShorCodeTrue && !isViewMode">
              <div class="item-value">{{ $t('IDC.orgOperator.operatorName') }}{{ organizationName }}</div>
            </div>
          </el-form-item>
        </el-col>
        <el-col v-bind="colOptions">
          <el-form-item prop="language" :label="$t('IDC.orgOperator.language')">
            <div v-if="isViewMode">
              <span v-text="getLanguageName(identityInfo.language)" />
            </div>
            <el-select
              id="language"
              v-else
              v-model="identityInfo.language"
              required
              :placeholder="$t('common.placeholder.select')"
            >
              <el-option
                v-for="item in languageOptions"
                :label="item.languageName"
                :value="item.languageCode"
                :key="item.languageCode"
              >
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>

        <el-col v-bind="colOptions">
          <el-form-item prop="accessChannel" :label="$t('IDC.orgOperator.accessChannel')" class="access-channel">
            <div v-if="isViewMode">
              <span v-text="getChannelName(identityInfo.accessChannel)" />
            </div>
            <el-checkbox-group
              v-else
              v-model="identityInfo.accessChannel"
              @change="changeAccessChanel"
              class="access-channel-checkbox"
            >
              <el-checkbox :id="item.label" v-for="item in channelOptions" :label="item.value" :key="item.value">
                {{ item.label }}
              </el-checkbox>
            </el-checkbox-group>
          </el-form-item>
        </el-col>
        <el-col v-bind="colOptions" v-if="identityInfo.accessChannel.includes('1007')">
          <el-form-item
            prop="operatorId"
            :label="$t('IDC.orgOperator.operatorID')"
            :rules="[{ validator: checkOperatorIdExist, trigger: 'blur' }, getRegexpValidateRule(getOperatorID)]"
            :required="true"
          >
            <div v-if="isViewMode">
              <span v-text="identityInfo.operatorId" />
            </div>
            <el-input
              id="operatorId"
              v-else
              v-model="identityInfo.operatorId"
              :placeholder="$t('common.placeholder.enter')"
            ></el-input>
          </el-form-item>
        </el-col>
        <el-col
          v-bind="colOptions"
          v-if="identityInfo.accessChannel.includes('1005') || identityInfo.accessChannel.includes('1010')"
        >
          <el-form-item
            prop="operatorName"
            :label="$t('IDC.orgOperator.userName')"
            :rules="[
              getRegexpValidateRule(getUserName),
              { validator: checkUserNameExist, trigger: ['blur', 'submit'] },
            ]"
            :required="true"
          >
            <div v-if="isViewMode">
              <span v-text="identityInfo.operatorName" />
            </div>
            <el-input
              id="userName"
              v-else
              v-model="identityInfo.operatorName"
              :placeholder="$t('common.placeholder.enter')"
            ></el-input>
          </el-form-item>
        </el-col>
        <el-col
          v-bind="colOptions"
          v-if="
            showMsisdn &&
            (identityInfo.accessChannel.includes('1007') || identityInfo.accessChannel.includes('1010')) &&
            isShowMSISDN
          "
        >
          <el-form-item
            prop="operatorMsisdn"
            :label="$t('IDC.orgOperator.msisd')"
            :rules="[{ required: true, trigger: 'blur', message: 'Please enter the msisdn' }, checkEntityIsExist()]"
          >
            <div v-if="isViewMode && isShowMSISDN">
              <span>{{ identityInfo.operatorMsisdn || '-' }}</span>
            </div>
            <el-input
              id="operatorMsisdn"
              v-else
              v-model="identityInfo.operatorMsisdn"
              :placeholder="$t('common.placeholder.enter')"
            ></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <div class="line title-padding"></div>
      <p class="title title-padding">{{ $t('IDC.orgOperator.assignNo') }}</p>
      <el-divider></el-divider>
      <el-row :gutter="20" class="title-padding">
        <el-col v-bind="colOptions">
          <el-form-item :label="$t('IDC.orgOperator.ruleProfile')">
            <div v-if="isViewMode">
              <span> {{ getProfileName(identityInfo.ruleProfile) || '-' }}</span>
            </div>
            <el-select
              id="language"
              v-else
              v-model="identityInfo.ruleProfile"
              :placeholder="$t('common.placeholder.select')"
            >
              <el-option v-for="item in profileList" :label="item.ruleProfileName" :value="item.id" :key="item.id">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
      </el-row>
      <el-divider class="border-line"></el-divider>
      <p class="title-padding padding-left" :style="{ marginBottom: isViewMode ? '0px' : '16px' }">
        {{ $t('IDC.orgOperator.role') }}
      </p>
      <el-row class="title-padding" v-show="false">
        <el-col v-bind="colOptions">
          <el-form-item prop="isRoles">
            <div>
              <el-input v-model="identityInfo.isRoles"> </el-input>
            </div>
          </el-form-item>
        </el-col>
      </el-row>
      <div class="title-padding">
        <assign-role
          ref="assignRoleTab"
          :selectedRoleList.sync="identityInfo.roles"
          :isViewMode="isViewMode"
        ></assign-role>
      </div>
    </el-form>
    <portal-short-code
      invoking-flag="createOrgOperator"
      :searchVisible.sync="dialogVisible"
      :short-code.sync="identityInfo.shortCode"
      :merchant-id.sync="identityInfo.merchantId"
      :merchant-name.sync="organizationName"
      @submitShortCode="submitShortCode"
    ></portal-short-code>
  </div>
</template>

<script>
import {
  getOrganizationList,
  checkShortCode,
  checkUserName,
  getRuleProfile,
  checkOperatorId,
  checkMsisdn,
} from '@/views/identifier-center/api/api-organization-operator';
import { getIdentityTypeEnum, loadLanguageList } from '@/views/identifier-center/api/api-organization';
import { getMsisdnRule } from '@/views/identifier-center/api/api-organization-operator';
import {
  inputRequiredValidateObj,
  len24StrValidateObj,
  len9To18StrValidateObj,
  integerValidateObj,
  nonSpecialCharValidateObj,
  numberAndCharacterValidateObj,
} from '@/common/validator/common';
import portalShortCode from '@/components/portal-short-code/index.vue';
import AssignRole from './assign-role.vue';
export default {
  name: 'IdentityInfo',
  components: { portalShortCode, AssignRole },
  props: {
    isViewMode: {
      type: Boolean,
      default: false,
    },
    identityInfo: {
      type: Object,
      default: () => {},
    },
  },

  data() {
    let checkOrgShortCode = (rule, value, callback) => {
      if (!value) {
        this.isShorCodeTrue = false;
        this.organizationName = '';
        return callback(new Error('Please select content'));
      }
      checkShortCode(this.identityInfo.shortCode)
        .then((res) => {
          if (res.header && res.header.success) {
            if (res.check === 'Exist') {
              if (!this.organizationName) {
                this.searchOrg();
              }
              return callback();
            } else if (res.check === 'NotExist') {
              this.isShorCodeTrue = false;
              return callback(new Error(this.$t('IDC.orgOperator.NotExist')));
            } else if (res.check === 'Invalid') {
              this.isShorCodeTrue = false;
              return callback(new Error(this.$t('IDC.orgOperator.Invalid')));
            }
          } else {
            this.isShorCodeTrue = false;
            return callback(new Error('Query Short Code error'));
          }
        })
        .catch(() => {
          return callback(new Error(this.$t('IDC.orgOperator.shortCodeTip')));
        });
    };
    return {
      dialogVisible: false,
      searchVisible: false,
      isShorCodeTrue: false,
      organizationName: '',
      trustLevel: '',
      getOperatorID: '',
      getUserName: '',
      identityRules: {
        shortCode: [nonSpecialCharValidateObj, { validator: checkOrgShortCode, trigger: 'blur' }, len24StrValidateObj],
        language: [
          {
            required: true,
            message: 'Please select content',
            trigger: 'change',
          },
        ],
        accessChannel: [{ required: true, message: 'Please check content', trigger: 'change' }],
        operatorId: [
          inputRequiredValidateObj,
          numberAndCharacterValidateObj,
          {
            min: 4,
            max: 8,
            message: 'Must be between 4 and 8 characters',
            trigger: 'blur',
          },
        ],
        ruleProfile: [
          {
            required: true,
            message: 'Please select content',
            trigger: 'blur',
          },
        ],
        operatorName: [
          inputRequiredValidateObj,
          numberAndCharacterValidateObj,
          {
            min: 1,
            max: 32,
            message: 'Must be between 1 and 32 characters',
            trigger: 'blur',
          },
        ],
        operatorMsisdn: [integerValidateObj, len9To18StrValidateObj],
        isRoles: [
          { required: true, message: 'Please select a role.' },
          { validator: this.checkAddRole, trigger: 'blur' },
        ],
      },
      identityRules1: {},
      languageOptions: [],
      channelOptions: [],
      profileList: [],
      showMsisdn: true,
      selectedRoleList: [],
      ruleProfileName: '',
      shortCode: '',
      isShowMSISDN: false,
      maxMSISDN: 10,
      minMSISDN: 0,
    };
  },
  computed: {},
  mounted() {
    this.initLanguage();
    this.getRuleProfile();
    this.channelOptions = this.initAccessChannel();
    this.orgGetRuleFn();
  },
  watch: {
    'identityInfo.roles': {
      handler(value) {
        if (value && value.length > 0) {
          this.identityInfo.isRoles = '1';
        } else {
          this.identityInfo.isRoles = '';
        }
      },
    },
  },
  activated() {},
  methods: {
    clearFn() {
      this.isShorCodeTrue = false;
      this.organizationName = '';
    },
    async orgGetRuleFn() {
      let res = await getMsisdnRule();
      if (
        (res.rules &&
          res.rules.OPERATOR_MSISDN_IDENTIFIER &&
          res.rules.OPERATOR_MSISDN_IDENTIFIER.defaultValue === 'true') ||
        (res.rules && res.rules.OPERATOR_MSISDN_IDENTIFIER && res.rules.OPERATOR_MSISDN_IDENTIFIER.defaultValue === '1')
      ) {
        this.isShowMSISDN = true;
      }
      this.maxMSISDN =
        res.rules &&
        res.rules.IC_MAXIMUM_LENGTH_MSISDN_NUMBER &&
        res.rules.IC_MAXIMUM_LENGTH_MSISDN_NUMBER.defaultValue;
      this.minMSISDN =
        res.rules &&
        res.rules.IC_MIMIMUM_LENGTH_MSISDN_NUMBER &&
        res.rules.IC_MIMIMUM_LENGTH_MSISDN_NUMBER.defaultValue;
      this.getOperatorID =
        res.rules && res.rules.OPERATOR_ID_VALIDATION_RULE && res.rules.OPERATOR_ID_VALIDATION_RULE.defaultValue;
      this.getUserName =
        res.rules && res.rules.USER_NAME_VALIDATION_RULE && res.rules.USER_NAME_VALIDATION_RULE.defaultValue;
    },
    checkEntityIsExist() {
      return {
        trigger: 'blur',
        validator: (rule, value, callback) => {
          if (value.length < this.minMSISDN || value.length > this.maxMSISDN) {
            callback(new Error(`Must be between ${this.minMSISDN} and ${this.maxMSISDN} characters`));
          } else {
            let params = {
              msisdn: this.identityInfo.operatorMsisdn,
            };
            checkMsisdn(params)
              .then((res) => {
                if (res.header && res.header.success) {
                  if (res.flag) {
                    callback();
                  } else if (!res.flag) {
                    let data = res.description;
                    return callback(new Error(JSON.stringify(data)));
                  }
                }
              })
              .catch(() => {});
          }
        },
      };
    },
    checkOperatorIdExist(rule, value, callback) {
      if (!value) {
        callback(new Error('Please enter the Operator ID'));
      } else if (this.identityInfo.shortCode !== '') {
        let data = {
          shortCode: this.identityInfo.shortCode,
          operatorId: value,
        };
        checkOperatorId(data)
          .then((res) => {
            if (res.header && res.header.success) {
              if (res.check === 'Exist') {
                callback(new Error('The Operator ID is existed'));
              } else if (res.check === 'Invalid') {
                callback(new Error('The Operator ID is invalid'));
              } else {
                callback();
              }
            } else {
              callback(new Error('Query Operator ID error'));
            }
          })
          .catch(() => {
            return callback(new Error('Query Operator ID Exception'));
          });
      } else {
        callback();
      }
    },
    // 变量接口校验规则
    getRegexpValidateRule(item) {
      if (item) {
        let expression = item;
        let rule = null;
        if (expression) {
          rule = {
            pattern: new RegExp(expression),
            message: `The regular expression is ${expression}.`,
            trigger: 'blur',
          };
        }
        return rule;
      } else {
        return {};
      }
    },
    initAccessChannel() {
      let accessChannel = [
        { value: '1005', label: 'Web' },
        { value: '1007', label: 'Handset' },
        { value: '1010', label: 'API' },
      ];
      return accessChannel;
    },
    initLanguage() {
      loadLanguageList().then((res) => {
        if (res.header && res.header.success) {
          this.languageOptions = res.list || [];
        }
      });
    },
    async getRuleProfile() {
      let param = {
        identityType: '',
      };
      let res = await getIdentityTypeEnum();
      if (res.header && res.header.success) {
        let list = res.dics;
        list.forEach((item) => {
          if (item.code === '3000') {
            param.identityType = item.code;
          }
        });
      }
      if (!param.identityType) {
        param.identityType = '3000';
      }
      getRuleProfile(param).then((res) => {
        if (res.header && res.header.success) {
          this.profileList = res.list || [];
          if (this.profileList.length > 0) {
            this.ruleProfileName = this.profileList[0].ruleProfileName;
            this.identityInfo.ruleProfileName = this.profileList[0].ruleProfileName;
            this.identityInfo.ruleProfile = this.profileList[0].id;
          }
        }
      });
    },
    getProfileName(id) {
      let item = this.profileList.find((item) => item.id === id);
      return item ? item.ruleProfileName : id;
    },
    getLanguageName(languageCode) {
      let item = this.languageOptions.find((item) => item.languageCode === languageCode);
      return item ? item.languageName : languageCode;
    },
    getChannelName(channelCodes) {
      let names = [];
      channelCodes.forEach((code) => {
        let item = this.channelOptions.find((channelItem) => code === channelItem.value);
        names.push(item ? item.label : code);
      });

      return names.join(',');
    },
    changeAccessChanel() {
      let supportChannels = this.initAccessChannel();
      let channels = this.identityInfo.accessChannel;
      if (channels.indexOf('1005') !== -1 || channels.indexOf('1007') !== -1) {
        this.channelOptions = [];
        this.channelOptions.push(supportChannels[0]);
        this.channelOptions.push(supportChannels[1]);
      }
      if (channels.indexOf('1010') !== -1) {
        this.channelOptions = [];
        this.channelOptions.push(supportChannels[2]);
      }
      if (channels == null || channels.length == 0) {
        this.channelOptions = supportChannels;
        this.identityInfo.operatorName = '';
        this.identityInfo.operatorId = '';
        this.identityInfo.operatorMsisdn = '';
      }

      if (channels.length == 1 && channels.includes('1007')) {
        this.identityInfo.operatorName = '';
      }
      if (channels.length == 1 && channels.includes('1005')) {
        this.identityInfo.operatorMsisdn = '';
        this.identityInfo.operatorId = '';
      }
      if (channels.length == 1 && channels.includes('1010')) {
        this.identityInfo.operatorId = '';
      }
    },
    submitShortCode() {
      this.isShorCodeTrue = true;
      this.$refs.saveTagInput.$refs.input.focus();
    },
    searchOrg() {
      if (!this.identityInfo.shortCode) {
        return;
      }
      let paramsObj = {
        shortCode: this.identityInfo.shortCode,
      };
      getOrganizationList(paramsObj)
        .then((res) => {
          if (res.header && res.header.success && res.organizationInfo) {
            this.isShorCodeTrue = true;
            this.organizationName = res.organizationInfo.organizationName;
            this.trustLevel = res.organizationInfo.trustLevel;
          } else {
            this.isShorCodeTrue = false;
          }
        })
        .catch(() => {})
        .finally(() => {
          this.isShorCodeTrue = true;
        });
    },
    checkAddRole(rule, value, callback) {
      let isCheck = this.$refs['assignRoleTab'].validate();
      if (isCheck) {
        callback();
      } else {
        callback(new Error('Please select a role.'));
      }
    },
    validate() {
      return new Promise((resolve) => {
        let promObj = new Promise((resolve) => {
          this.$refs['identityInfo'].validate((valid) => {
            return resolve(valid);
          });
        });

        promObj.then((identityFlag) => {
          return resolve(identityFlag);
        });
      });
    },
    checkUserNameExist(rule, value, callback) {
      if (!value) {
        callback(new Error('Please enter the User Name'));
      } else if (this.identityInfo.shortCode !== '') {
        let parm = {
          shortCode: this.identityInfo.shortCode,
          userName: value,
        };
        checkUserName(parm)
          .then((res) => {
            if (res.header && res.header.success) {
              if (res.check === 'Exist') {
                callback(new Error('The User Name is existed'));
              } else if (res.check === 'Invalid') {
                callback(new Error('The User Name is invalid'));
              } else {
                callback();
              }
            } else {
              callback(new Error('Query User Name error'));
            }
          })
          .catch(() => {
            callback(new Error('Query User Name Exception'));
          });
      } else {
        callback();
      }
    },
  },
};
</script>
<style lang="scss" scoped>
