  .shortCode {
    height: 0px;
    color: $--status_color--notice;
    font-size: 12px;
    padding-bottom: 24px;
    .item-value {
      line-height: 20px;
      padding-top: 8px;
    }
  }
  .title {
    font-weight: bold;
    color: $--text_color--emphasis;
    border-radius: 4px;
  }
  .title-padding {
    padding: 0px 20px;
    margin-top: 20px;
    flex-wrap: wrap;
    display: flex;
    /deep/.el-form-item .el-form-item__content {
      margin: 0;
      .el-icon-circle-close {
        right: 16px !important;
      }
    }
    .icon-starkey {
      &::before {
        content: '*';
        color: #f56c6c;
        margin-right: 4px;
        font-size: 22px;
        position: absolute;
        left: 29px;
      }
      /deep/ .el-form-item__label {
        margin-left: 11px !important;
      }
    }
  }
  .access-channel {
    &.is-error {
      margin-bottom: 0;
    }
    .access-channel-checkbox {
      margin-bottom: 13px;
    }
    /deep/.el-form-item__error {
      top: 16px;
    }
  }

  .line {
    height: 16px;
    background-color: $--global-background_color;
    margin-bottom: 20px;
    margin-top: 0;
  }
  .line-view-bottom {
    height: 16px;
    background-color: $--fill_color--regular;
    margin-top: 25px;
  }
  .padding-left {
    margin-left: 22px;
    padding-left: 4px;
    color: $--fill_color--emphasis;
    font-size: 14px;
    font-weight: bold;
    &::before {
      content: '|';
      width: 2px;
      height: 16px;
      font-weight: bold;
      color: $--primary_color;
      margin-right: 4px;
    }
  }
  /deep/.border-line {
    width: calc(100% - 40px);
    margin: 10px 20px;
  }
}
/deep/ .el-dialog {
  .el-dialog__footer {
    margin-top: 20px;
  }
}
</style>
