<template>
  <div class="add-assign-role">
    <el-form ref="roleRef">
      <el-form-item required prop="addRole">
        <template slot="label">
          <el-button
            id="addRole"
            v-if="!isViewMode"
            type="primary"
            plain
            size="small"
            icon="el-icon-plus"
            @click="searchRoleVisible = true"
          >
            {{ $t('IDC.orgOperator.addRole') }}
          </el-button>
        </template>
      </el-form-item>
    </el-form>

    <el-table
      id="selectedRoleTable"
      size="small"
      :data="isViewMode ? selectedRoleList : tempSelectedList"
      empty-text="No records found."
      :style="{ marginTop: isViewMode ? '0px' : '12px' }"
      max-height="450"
    >
      <el-table-column prop="roleName" :label="$t('IDC.orgOperator.roleName')" min-width="20%" show-overflow-tooltip>
        <template slot-scope="scope">{{ scope.row.roleName || '-' }} </template>
      </el-table-column>
      <el-table-column
        prop="effectiveTime"
        :label="$t('IDC.orgOperator.effectiveDate')"
        min-width="25%"
        show-overflow-tooltip
      >
        <template slot-scope="scope">
          <span v-if="isViewMode" v-text="scope.row.effectiveTime" />
          <el-date-picker
            :id="'effectiveTime' + scope.$index"
            v-else
            size="small"
            style="width: 152px"
            v-model="scope.row.effectiveTime"
            format="dd-MM-yyyy"
            value-format="dd-MM-yyyy"
            type="date"
            :picker-options="pickerOptionsStart(scope.row)"
          />
        </template>
      </el-table-column>
      <el-table-column :label="$t('IDC.orgOperator.expirationDate')" min-width="25%" show-overflow-tooltip>
        <template slot-scope="scope">
          <span v-if="isViewMode" v-text="scope.row.expiredTime" />
          <el-date-picker
            :id="'expiredTime' + scope.$index"
            v-else
            size="small"
            style="width: 152px"
            v-model="scope.row.expiredTime"
            format="dd-MM-yyyy"
            value-format="dd-MM-yyyy"
            type="date"
            :picker-options="pickerOptionsEnd(scope.row)"
          />
        </template>
      </el-table-column>
      <el-table-column
        prop="description"
        :label="$t('IDC.orgOperator.description')"
        min-width="20%"
        show-overflow-tooltip
      >
        <template slot-scope="scope">{{ scope.row.description || '-' }} </template>
      </el-table-column>
      <el-table-column :label="$t('IDC.orgOperator.operation')" width="150" show-overflow-tooltip v-if="!isViewMode">
        <template slot-scope="scope">
          <el-button
            :id="'delete' + scope.$index"
            size="small"
            plain
            type="text"
            @click="deleteRole(scope.$index, scope.row)"
            >{{ $t('common.button.delete') }}</el-button
          >
        </template>
      </el-table-column>
    </el-table>
    <el-dialog
      :title="$t('IDC.organizationOperatorDetail.label.AddRole')"
      :visible.sync="searchRoleVisible"
      width="800px"
      :close-on-click-modal="false"
      :destroy-on-close="false"
    >
      <table-page
        v-if="searchRoleVisible"
        ref="roleDialogTable"
        idKey="roleCode"
        size="small"
        :isCheck="true"
        :isCheckMemory="false"
        :isSingleCheck="false"
        :has-pagination="false"
        :pagination="paginationConfig"
        :columns="roleColumns"
        :initChecks="displayRoleList"
        :hasPagination="false"
        :getTableData="getRoleTableData"
        @selection-change="handleRoleSelection"
      >
      </table-page>
      <div slot="footer">
        <el-button id="cancelSearchRole" size="small" @click="searchRoleVisible = false">{{
          $t('common.button.cancel')
        }}</el-button>
        <el-button id="ok" type="primary" size="small" @click="addRole" v-preventReClick>{{
          $t('common.button.confirm')
        }}</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { queryOrgOprRoleList } from '@/views/identifier-center/api/api-organization-operator';
import { checkRoleRelationship } from '@/views/identifier-center/api/api-organization';
import TablePage from '@/components/tablePage';
import { formatStrToDate2, dateFormat } from '@/common/utils/date-time-utils';
export default {
  name: 'AssignRole',
  components: { TablePage },
  props: {
    isViewMode: {
      type: Boolean,
      default: true,
    },
    selectedRoleList: {
      type: Array,
      default: () => [],
    },
  },
  data() {
    return {
      searchRoleVisible: false,
      availableRoleList: [],
      roleItems: [],
      roleColumns: [
        {
          label: this.$t('IDC.orgOperator.roleName'),
          prop: 'roleName',
          align: 'left',
          'sort-method': (a, b) => {
            return a.roleName > b.roleName ? 1 : -1;
          },
        },
        {
          label: this.$t('IDC.orgOperator.description'),
          prop: 'description',
          align: 'left',
          'sort-method': (a, b) => {
            return a.description > b.description ? 1 : -1;
          },
        },
      ],
      paginationConfig: {
        total: 0,
        pageIndex: 1,
        pageSize: 10,
      },
      displayRoleList: [],
      tempSelectedList: [],
    };
  },
  watch: {
    selectedRoleList: {
      handler(val) {
        this.tempSelectedList = val;
      },
    },
  },
  created() {
    this.tempSelectedList = this.selectedRoleList;
  },
  methods: {
    getRoleTableData() {
      let queryParams = {};
      queryParams.roleType = '30';
      return new Promise((resolve) => {
        queryOrgOprRoleList(queryParams)
          .then((res) => {
            if (res.header && res.header.success) {
              this.availableRoleList = res.grantRoleList || [];
              let tempRes = {
                header: res.header,
                list: this.availableRoleList,
              };
              resolve(tempRes);
            }
          })
          .catch(() => {
            resolve();
          });
      });
    },
    pickerOptionsStart(row = {}) {
      return {
        disabledDate(time) {
          if (row.expiredTime) {
            if (time.getTime() > formatStrToDate2(row.expiredTime).getTime()) {
              return true;
            }
            if (time.getTime() < Date.now() - 8.64e7) {
              return true;
            }
          } else {
            if (time.getTime() < Date.now() - 8.64e7) {
              return true;
            }
          }
        },
      };
    },
    pickerOptionsEnd(row = {}) {
      return {
        disabledDate(time) {
          if (row.effectiveTime) {
            return time.getTime() > Date.now || time.getTime() < formatStrToDate2(row.effectiveTime).getTime();
          } else {
            return time.getTime() <= Date.now();
          }
        },
      };
    },
    deleteRole(index, row) {
      let tip = this.$t('IDC.orgOperator.deleteTip', { obj: row.roleName });
      this.$confirm(tip, this.$t('common.button.delete'), {
        confirmButtonText: this.$t('common.button.confirm'),
        cancelButtonText: this.$t('common.button.cancel'),
        closeOnClickModal: false,
        customClass: 'delete-confirm',
        type: 'warning',
      })
        .then(() => {
          let roleItem = this.tempSelectedList.find((item) => item.roleId === row.roleId);
          this.tempSelectedList.splice(index, 1);
          this.$emit('selectedRoleList', this.tempSelectedList);
          this.availableRoleList.push(roleItem);
        })
        .catch(() => {})
        .finally(() => {});
    },
    handleRoleSelection(val) {
      this.roleItems = val;
      if (val.length > 1) {
        let selectedRoleIdList = [];
        this.roleItems.forEach((item) => selectedRoleIdList.push(item.roleId));
        let param = {};
        param.roleIds = selectedRoleIdList;
        this.checkSelectRole(param, val[val.length - 1]);
      }
    },
    checkSelectRole(param, val) {
      checkRoleRelationship(param).then((res) => {
        if (res.header && res.header.success) {
          // 根据返回结果进行判定
          if (!res.checkResult) {
            this.$refs.roleDialogTable.$refs['tableRef'].toggleRowSelection(val, false);
            this.$modal.alertWarning(res.errorMsg);
          }
        }
      });
    },
    addRole() {
      this.displayRoleList = [];
      this.tempSelectedList = [];
      this.roleItems.forEach((role) => {
        let index = this.availableRoleList.findIndex((item) => Object.is(item.roleId, role.roleId));
        let roleItem = this.availableRoleList[index];
        if (roleItem) {
          this.$set(roleItem, 'effectiveTime', dateFormat(new Date().getTime(), 'DD-MM-YYYY'));
          this.$set(roleItem, 'expiredTime', '31-12-2099');
          roleItem.roleId = roleItem.roleCode;
          let roleSelected;
          if (this.tempSelectedList.length > 0) {
            roleSelected = this.tempSelectedList.find((item) => {
              return item.roleId === roleItem.roleId;
            });
            if (!roleSelected) {
              this.tempSelectedList.push(roleItem);
            }
          } else {
            this.tempSelectedList.push(roleItem);
          }
          // 更新可选列表
          this.availableRoleList.splice(index, 1);
        }
      });
      this.displayRoleList = this.tempSelectedList;
      this.$emit('update:selectedRoleList', this.tempSelectedList);
      this.searchRoleVisible = false;
    },
    validate() {
      let flag = true;
      if (this.tempSelectedList.length === 0) {
        flag = false;
        this.$message.warning('Please select a role.');
      }
      return flag;
    },
  },
  computed: {},
  mounted() {},
};
</script>

<style lang="scss" scoped>
.role-configuration {
  font-family: HuaweiSans-Bold, 'HuaweiSans';
  font-size: 14px;
  color: $--text_color--emphasis;
  font-weight: 700;
  margin-bottom: 16px;
}
.dialog-sub-title {
  width: 292px;
  height: 15px;
  font-size: 12px;
  color: $--line_color--strong;
  font-weight: 400;
  margin-bottom: 20px;
}

.search-btn-container {
  display: flex;
  justify-content: flex-end;
  margin-top: 32px;
}
.add-assign-role {
  margin-bottom: 24px;
  width: 100%;
  /deep/ .el-form-item__label:before {
    margin-top: 5px;
  }
  /deep/ .el-form-item {
    margin-bottom: 0;
  }
}
</style>
